<template>
  <b-modal
    id="registration"
    hide-header-close
    no-close-on-backdrop
    no-fade
    size="lg"
    ref="modalDetail"
  >
    <template #modal-title>Thêm phiếu đăng ký</template>
    <template #modal-footer>
      <button @click="saveRegistration" class="btn btn-primary-soft">
        <i class="fa-thin fa-floppy-disk"></i> Lưu
      </button>
      <button class="btn btn-secondary" @click="$bvModal.hide('registration')">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
    </template>
    <div class="row">
      <div class="col-6">
        <div class="form-group">
          <label for="">Họ và tên</label>
          <input
            v-model="regiation.reg_fullname"
            type="text"
            placeholder="Họ và tên"
            class="form-control"
          />
        </div>
      </div>
      <div class="col-6">
        <div class="form-group">
          <label for="">Chức vụ</label>
          <input
            v-model="regiation.reg_position"
            type="text"
            placeholder="Chức vụ"
            class="form-control"
          />
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-6">
        <div class="form-group">
          <label for="">Email</label>
          <input
            v-model="regiation.reg_email"
            type="text"
            placeholder="Email"
            class="form-control"
          />
        </div>
      </div>
      <div class="col-6">
        <div class="form-group">
          <label for="">Số điện thoại</label>
          <input
            v-model="regiation.reg_phone"
            type="text"
            placeholder="Số điện thoại"
            class="form-control"
            maxlength="15"
          />
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-6">
        <div class="form-group">
          <label for="">CMND/CCCD</label>
          <input
            v-model="regiation.card_id"
            type="text"
            placeholder="CMND/CCCD"
            class="form-control"
          />
        </div>
      </div>
      <div class="col-6">
        <div class="form-group">
          <label for="">Ngày cấp CCCD/CMND</label>
          <input
            v-model="regiation.card_date_create"
            type="text"
            placeholder="Ngày cấp CCCD/CMND"
            class="form-control"
          />
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-6">
        <div class="form-group">
          <label for="">Nơi cấp CMND/CCCD</label>
          <input
            v-model="regiation.card_issued"
            type="text"
            placeholder="Nơi cấp CMND/CCCD"
            class="form-control"
          />
        </div>
      </div>

      <div class="col-6">
        <div class="form-group">
          <label for="">Mã đăng ký</label>
          <input
            v-model="regiation.ma_tb"
            type="text"
            placeholder="Mã đăng ký"
            class="form-control"
          />
        </div>
      </div>
    </div>
  </b-modal>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      regiation: {
        reg_id: 0,
        reg_fullname: "",
        reg_email: "",
        contract_id: 0,
        card_id: "",
        card_date_create: "",
        card_issued: "",
        reg_phone: 0,
        reg_position: "",
        ma_tb: "",
      },
      isUpdate: false,
      index: 0,
    };
  },
  methods: {
    show(regiation = [], index = 0) {
      if (regiation.reg_fullname != null) {
        this.regiation = {
          ...regiation,
        };
        this.index = index;
        this.isUpdate = true;
      } else {
        this.regiation = {
          reg_id: 0,
          reg_fullname: "",
          reg_email: "",
          contract_id: 0,
          card_id: "",
          card_date_create: "",
          card_issued: "",
          reg_phone: 0,
          reg_position: "",
          ma_tb: "",
        };
        this.isUpdate = false;
      }

      this.$bvModal.show("registration");
    },
    async saveRegistration() {
      if (this.checkValidate()) {

        delete this.regiation.pivot;
        if (!this.isUpdate) {
          await axios.post("registration", this.regiation).then((response) => {
            this.$emit("saveRegistration", response.data, this.isUpdate, this.index);
            this.$bvModal.hide("registration");
          });
        } else {
          await axios.put(`registration/${this.regiation.reg_id}`, this.regiation).then((response) => {
            this.$emit("saveRegistration", response.data, this.isUpdate, this.index);
            this.$bvModal.hide("registration");
          });
        }
      } else this.$toast.warning("Chưa nhập đủ dữ liệu");
    },

    checkValidate() {
      if (this.regiation.reg_fullname == "") return false;
      return true;
    },
  },
};
</script>
