<template>
  <tr>
    <td>{{ index }}</td>
    <td>
      <input type="text" class="form-control" v-model="detail.detail_content" />
    </td>
    <td>
      <input type="text" class="form-control" v-model="detail.detail_quantity" />
    </td>
    <td><input type="text" value="1" class="form-control" /></td>
    <td>
      <template v-if="detail.device.device_price > 0">
        <input type="text" class="form-control" v-model="detail.device.device_price" />
      </template>
      <template v-else>
        <input
          type="text"
          disabled
          :value="detail.device.device_name"
          class="form-control"
        />
      </template>
    </td>
    <td></td>
    <td>
      <button class="btn btn-sm btn-primary me-1">
        <i class="fa-thin fa-pen-to-square"></i>
      </button>
      <button class="btn btn-sm btn-danger">
        <i class="fa-thin fa-trash-can"></i>
      </button>
    </td>
  </tr>
</template>

<script>
export default {
    props:{
        detail:Array
    }
};
</script>
