<template>
  <tbody>
    <tr>
      <td>{{ index + 1 }}</td>
      <td>{{ registration.reg_fullname }}</td>
      <td>{{ registration.reg_position }}</td>
      <td>{{ registration.reg_phone }}</td>
      <td>{{ registration.reg_email }}</td>
      <td>{{ registration.ma_tb }}</td>
      <td>
        <button
          class="btn btn-primary-soft btn-sm"
          @click="sendRegistration(registration, index)"
        >
          <i class="fa-thin fa-pen-to-square"></i>
        </button>
        <button
          class="btn btn-danger-soft btn-sm ms-1"
          @click="deleteRegistration(registration, index)"
        >
          <i class="fa-thin fa-trash-can"></i>
        </button>
      </td>
    </tr>
  </tbody>
</template>

<script>
export default {
  props: {
    registration: Object,
    index: Number,
  },
  methods: {
    sendRegistration(registration, index) {
      this.$emit("sendRegistration", registration, index);
    },
    deleteRegistration(detail, index) {
      this.$emit("deleteRegistration", detail, index);
    },
  },
};
</script>
