<template>
  <b-modal
    v-if="isShow"
    id="modalRegistrationsList"
    title="Danh sách phiếu đăng ký"
    no-fade
    size="xl"
    hide-footer
  >
    <div class="form-group mb-1">
      <input
        v-model="keyword"
        type="text"
        placeholder="Tìm kiếm tên phiếu đăng ký"
        class="form-control"
      />
    </div>
    <div class="card p-0 border-0">
      <!-- <pre>
        {{ registrations }}
      </pre> -->
      <div class="card-body overflow-auto" style="height: 400px">
        <table class="table">
          <thead class="table-dark">
            <tr>
              <th class="rounded-start" scope="col">#</th>
              <th  width="25%">Họ và tên</th>
              <th>SĐT</th>
              <!-- <th>CCCD</th>
              <th>Ngày cấp</th> -->
              <!-- <th>Nơi cấp</th> -->
              <th width="5%" class="text-wrap">Mã TB</th>
              <th>Tên HĐ</th>
              <th class="rounded-end"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in filterdRegistrations" :key="item.reg_id">
              <td>{{ item.reg_id }}</td>
              <td>
                <strong class="text-primary">{{ item.reg_fullname }}</strong>
              </td>
              <td>{{ item.reg_phone }}</td>
              <!-- <td>{{ item.card_id }}</td>
              <td>{{ item.card_date_create }}</td> -->
              <!-- <td>{{ item.card_issued }}</td> -->
              <td>{{ item.ma_tb }}</td>
              <td>
                <template v-if="item.contracts">
                  <small>
                  {{ item.contracts.company.company_name }}
                </small>
                </template>
              </td>
              <td>
                <button class="btn btn-primary-soft btn-sm" @click="choose(item)">
                  <i class="fa-regular fa-circle-arrow-right"></i>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
        <!-- <button @click="test" class="btn btn-primary-soft btn-sm">Lưu</button> -->
      </div>
    </div>
  </b-modal>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      isShow: true,
      registrations: [],
      keyword: "",
    };
  },
  created() {},
  computed: {
    filterdRegistrations() {
      return this.registrations.filter((item) => {
        return item.reg_fullname.toLowerCase().indexOf(this.keyword.toLowerCase()) !== -1;
      });
    },
  },
  methods: {
    // test(item) {
    //   try{
    //     return item.contract.company.company_name;
    //   }
    //   catch{}
    // },
    choose(registration) {
      this.$emit("reviceRegistration", registration);
      this.$bvModal.hide("modalRegistrationsList");
    },
    show() {
      this.isShow = true;
      this.getRegistrations();
      this.$bvModal.show("modalRegistrationsList");
    },
    async getRegistrations() {
      this.$econtract.showLoading();
      await axios.get("registration").then((response) => {
        this.registrations = response.data;
        this.$econtract.hideLoading();
      });
    },
  },
};
</script>
