<template>
  <b-modal
    id="modalTemplate"
    title="Danh sách mẫu hợp đồng"
    hide-header-close
    no-close-on-backdrop
    no-fade
  >
 
    <template #modal-footer>
      <button @click="saveTemplate" class="btn btn-primary-soft">
        <i class="fa-thin fa-floppy-disk"></i> Lưu
      </button>
      <button class="btn btn-secondary" @click="$bvModal.hide('modalTemplate')">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
    </template>
    <div class="row mb-1">
      <div class="col-12">
        <div class="form-group">
          <input
            type="text"
            placeholder="Nhập từ khóa để tìm kiếm..."
            class="form-control"
            v-model="keyword"
          />
        </div>
      </div>
    </div>
    <div class="card border-0">
      <div class="card-body p-0 overflow-auto" style="height: 400px">
        <ul class="list-group">
          <li class="list-group-item" v-for="item in templateSearch" :key="item.temp_id">
            <input
              class="form-check-input me-1"
              type="checkbox"
              :value="item.temp_id"
              :id="item.temp_id"
              v-model="templateSelected"
            />
            <label class="form-check-label" :for="item.temp_id">{{
              item.temp_name
            }}</label>
          </li>
        </ul>
      </div>
    </div>
  </b-modal>
</template>

<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  props: {
    templateSelecteds: Array,
  },
  data() {
    return {
      // templates: [],
      keyword: "",
      templateSelected: [],
    };
  },
  computed: {
    ...mapGetters("templates", ["templates"]),
    templateSearch() {
      return this.templates.filter((item) => {
        return item.temp_name.toLowerCase().indexOf(this.keyword.toLowerCase()) !== -1;
      });
    },
  },
  created() {
    this.templateSelected = this.templateSelecteds;
  },
  methods: {
    saveTemplate() {
      if (this.templateSelected.length > 0) {
        this.$emit("saveTemplate", this.templateSelected);
        this.$toast.success("Thêm mẫu thành công");
      }
      this.$bvModal.hide("modalTemplate");
    },
    async getTemplates() {
      this.$econtract.showLoading();

      await this.$store.dispatch("templates/getTemplates");
      console.log(this.templates)
      this.$econtract.hideLoading();
      // await axios.get(`/templates`).then((response) => {
      //   this.templates = response.data;
      //   this.$econtract.hideLoading();
      // });
    },
    show() {
      this.getTemplates();
      this.$bvModal.show("modalTemplate");
    },
  },
};
</script>
