<template>
  <b-modal
    id="modal-contract-option"
    hide-header-close
    no-close-on-backdrop
    no-fade
    size="lg"
    title="Tùy chỉnh thông tin hợp đồng"
  >
    <div class="row">
      <div class="col-6">
        <div class="form-group">
          <label for="">Người đại diện</label>
          <input
            type="text"
            placeholder="Tên người đại  diện"
            v-model="ContractOption.owner_name"
            class="form-control"
          />
        </div>
      </div>
      <div class="col-6">
        <div class="form-group">
          <label for="">Chức vụ</label>
          <input
            type="text"
            placeholder="Chức vụ đại  diện"
            v-model="ContractOption.owner_position"
            class="form-control"
          />
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-6">
        <div class="form-group">
          <label for="">Số CCCD</label>
          <input
            type="text"
            placeholder="CCCD/CMND Người đại diện"
            v-model="ContractOption.owner_card_id"
            class="form-control"
          />
        </div>
      </div>
      <div class="col-6">
        <div class="form-group">
          <label for="">Ngày Cấp</label>
          <input
            type="text"
            placeholder="Ngày cấp"
            v-model="ContractOption.owner_card_date"
            class="form-control"
          />
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-12">
        <div class="form-group">
          <label for="">Nơi cấp</label>
          <input
            type="text"
            placeholder="Nơi cấp CCCD"
            v-model="ContractOption.owner_card_issuedby"
            class="form-control"
          />
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-12">
        <div class="form-group">
          <label for="">Giấy ủy quyền số:</label>
          <input
            type="text"
            placeholder="Giấy ủy quyền số:"
            v-model="ContractOption.owner_authority"
            class="form-control"
          />
        </div>
      </div>
    </div>

    <template #modal-footer>
      <button @click="store" class="btn btn-primary-soft">
        <i class="fa-thin fa-floppy-disk"></i> Lưu
      </button>
      <button class="btn btn-secondary" @click="close">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
    </template>
  </b-modal>
</template>

<script>
import moment from "moment";
export default {
  data() {
    return {
      ContractOption: {
        owner_name: "",
        owner_position: "",
        owner_authority: "",
        contract_id: 0,
        owner_card_issuedby: "",
        owner_card_date: moment().format("DD/MM/YYYY"),
        owner_card_id: 0,
      },
    };
  },
  methods: {
    store() {
      this.$emit("updateContractOption", this.ContractOption);
      this.close();
    },
    show() {
      this.$bvModal.show("modal-contract-option");
    },
    close() {
      this.$bvModal.hide("modal-contract-option");
    },
  },
};
</script>
