<template>
  <b-modal
    id="bv-modal-example"
    hide-header-close
    no-close-on-backdrop
    no-fade
    size="lg"
    ref="modalDetail"
    ignore-enforce-focus-selector="span.select2-container"
  >
    <template #modal-title>Thêm dịch vụ </template>
    <template #modal-footer>
      <button @click="saveDetail" class="btn btn-primary-soft">
        <i class="fa-thin fa-floppy-disk"></i> Lưu
      </button>
      <button class="btn btn-secondary" @click="$bvModal.hide('bv-modal-example')">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
    </template>
    <b-row>
      <b-col cols="6">
        <div class="form-group">
          <label for="">Nhóm dịch vụ</label>
          <select2
            v-model="Detail.cate_id"
            :options="selects.categories"
            :settings="settings"
          />
        </div>
      </b-col>
      <b-col cols="6">
        <div class="form-group">
          <label for="">Dịch vụ</label>
          <select2
            v-model="Detail.product_id"
            :options="product"
            :settings="settings"
            @change="getProduct()"
          />
        </div>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="6">
        <div class="form-group">
          <label for="">Tiền trước thuế</label>
          <money
            class="form-control"
            v-bind="money"
            v-model="Detail.detail_product_price"
          />
        </div>
      </b-col>
      <b-col cols="6">
        <div class="form-group">
          <label for="">Mã thuê bao</label>
          <input
            placeholder="Mã thuê bao"
            type="text"
            class="form-control"
            v-model="Detail.ma_tb"
          />
        </div>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="6">
        <div class="form-group">
          <label for="">Mã BHXH/Số lượng hóa đơn</label>
          <input
            placeholder="Mã BHXH/Số lượng hóa đơn"
            type="text"
            class="form-control"
            v-model="Detail.extra1"
          />
        </div>
      </b-col>
      <b-col cols="6">
        <div class="form-group">
          <label for="">Ký hiệu/Mẫu số</label>
          <input
            placeholder="Ký hiệu/Mẫu số"
            type="text"
            class="form-control"
            v-model="Detail.extra2"
          />
        </div>
      </b-col>
    </b-row>
    <b-row class="mt-1">
      <b-col cols="6">
        <div class="form-check form-switch">
          <input
            class="form-check-input"
            type="checkbox"
            role="switch"
            v-model="Detail.is_device"
          />
          <label class="form-check-label" for="flexSwitchCheckChecked">Thiết bị</label>
        </div>
        <div v-show="Detail.is_device"></div>
      </b-col>

      <b-col cols="6">
        <div class="form-check form-switch">
          <input
            class="form-check-input"
            type="checkbox"
            role="switch"
            v-model="Detail.is_payment"
          />
          <label class="form-check-label" for="flexSwitchCheckChecked">Tính tiền</label>
        </div>
        <div v-show="Detail.is_payment"></div>
      </b-col>
    </b-row>
    <b-row>
      <b-col class="12">
        <div class="form-group">
          <label for="">Nội dung</label>

          <textarea
            @click="autoComplete.isShow = true"
            type="text"
            placeholder="Nội dung"
            data-bs-toggle="dropdown"
            class="form-control"
            v-model="Detail.detail_content"
            rows="1"
            @keydown="changeDetail"
          />
          <ul class="dropdown-menu bg-blur">
            <li v-for="item in filterItem" :key="item" @click="setItem(item)">
              <a class="dropdown-item hand" :title="item">{{ item }}</a>
            </li>
            <li v-if="filterItem.length == 0">
              <div class="text-danger text-center">Không có dữ liệu</div>
            </li>
          </ul>
        </div>
      </b-col>
    </b-row>

    <b-row>
      <b-col class="12">
        <div class="form-group">
          <label for="">Ghi chú</label>
          <textarea
            type="text"
            placeholder="Ghi chú"
            class="form-control"
            v-model="Detail.detail_note"
            rows="3"
          />
        </div>
      </b-col>
    </b-row>
  </b-modal>
</template>

<script>
import Select2 from "v-select2-component";
import { Money } from "v-money";
import { mapGetters } from "vuex";
export default {
  components: {
    Select2,
    Money,
  },
  data() {
    return {
      Detail: {
        cate_id: 0,
        product_id: 0,
        detail_product_price: 0,
        detail_product_component: 0,
        detail_content: "",
        detail_unit: "Cái",
        detail_quantity: 1,
        is_device: false,
        extra1: "",
        extra2: "",
        detail_note: null,
        is_payment: 1,
      },
      autoComplete: {
        items: [
          "Dịch vụ chữ ký số công cộng VNPT-CA ({key})",
          "HD{key} ({key} hóa đơn)",
          "Chứng từ thuế thu nhập cá nhân HD{key} ({key} chứng từ)",
          "Dịch vụ kê khai BHXH VNPT-BHXH ({key})",
          "Gia hạn dịch vụ giám sát thành trình VNPT-TRACKING ({key})",
          "Gói cước dịch vụ VNPT-CA trên sim Vinaphone ({key})",
          "Dịch vụ chữ ký số công cộng VNPT-CA tích hợp VNPT-BHXH ({key})",
        ],
        isShow: false,
      },

      selects: {
        categories: [],
        products: [],
        devices: [],
      },
      settings: {
        placeholder: "--Hãy chọn dữ liệu--",
        theme: "bootstrap-5",
        width: "100%",
      },

      isUpdate: false,
      index: 0,
      productName: "",
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "",
        suffix: " ",
        precision: 4,
        masked: false,
      },
    };
  },
  computed: {
    ...mapGetters("products", ["products"]),
    ...mapGetters("categories", ["categories"]),
    product() {
      return this.selects.products.filter((product) => {
        return product.cate_id == this.Detail.cate_id;
      });
    },
    filterItem() {
      this.autoComplete.isShow = true;
      return this.autoComplete.items.filter((x) => {
        if (this.Detail.detail_content) {
          return x.toLowerCase().indexOf(this.Detail.detail_content.toLowerCase()) !== -1;
        }
      });
   },
  },
  mounted() {},
  methods: {
    changeDetail(){
      this.autoComplete.isShow = true;
      return this.autoComplete.items.filter((x) => {
        if (this.Detail.detail_content) {
          return x.toLowerCase().indexOf(this.Detail.detail_content.toLowerCase()) !== -1;
        }
      });
    },  
    saveDetail() {
      // Tìm sản phẩm dựa trên product_id của Detail
      const product = this.product.find(
        (product) => product.id == this.Detail.product_id
      );

      if (!product) {
        this.$toast.warning("Sản phẩm không tồn tại");
        return;
      }

      // Thiết lập detail_unit dựa trên is_device
      this.Detail.detail_unit = this.Detail.is_device ? "Cái" : "Gói";

      // Lấy tên danh mục dựa trên cate_id của sản phẩm
      const category = this.getCategory(product.cate_id);
      const cateName = category ? category.text : "";

      // Thiết lập detail_content dựa trên is_device
      if (this.Detail.detail_content.length == 0)
        this.Detail.detail_content = this.Detail.is_device
          ? cateName
          : `${cateName} ${product.text}`;

      // Kiểm tra điều kiện và phát sự kiện nếu hợp lệ
      const isDetailValid = this.Detail.cate_id > 0 && this.Detail.product_id > 0;

      if (isDetailValid) {
        this.Detail['product']=product;
        this.$emit("saveDetail", this.Detail, this.isUpdate, this.index);
        this.$bvModal.hide("bv-modal-example");
      } else {
        this.$toast.warning("Bạn chưa chọn dịch vụ");
      }
    },
    setItem(item) {
      this.Detail.detail_content = item.replace(/\{key\}/g, "");
      this.autoComplete.isShow = false;
    },
    onModalHide() {},
    show(detail = [], index = 0) {
      if (detail.cate_id > 0) {
        this.index = index;
        this.isUpdate = true;
        this.Detail = {
          ...detail,
        };
      } else {
        this.Detail = {
          cate_id: 0,
          product_id: 0,
          detail_product_price: 0,
          detail_product_component: 0,
          is_device: false,
          is_payment: true,
        };
        this.isUpdate = false;
      }
      this.getCategories();
      this.getProducts();
      this.$bvModal.show("bv-modal-example");
    },
    getProduct() {
      let product = this.product.filter((product) => {
        return product.id == this.Detail.product_id;
      })[0];

      const category = this.getCategory(product.cate_id);
      const cateName = category ? category.text : "";

      this.Detail.detail_product_price = product.product_price;
      this.Detail.detail_product_component = product.product_component_price;
      this.Detail.product_id = product.id;
      // this.Detail.device_id = !this.Detail.is_device ? 0 : this.Detail.device_id;
      this.Detail.detail_content = this.Detail.is_device ? cateName : `${cateName}`;

      this.Detail.detail_quantity = 1;
    },

    getCategory(id) {
      return this.selects.categories.filter((cate) => {
        return cate.id == id;
      })[0];
    },
    async getCategories() {
      await this.$store.dispatch("categories/getCategories");
      this.selects.categories = this.categories.map((c) => {
        return {
          id: c.cate_id,
          text: c.cate_name,
        };
      });
    },
    async getProducts() {
      await this.$store.dispatch("products/getProducts");
      this.selects.products = this.products.map((c) => {
        return {
          id: c.product_id,
          text: c.product_name,
          cate_id: c.cate_id,
          product_price: c.product_price,
          product_component_price: c.product_component_price,
        };
      });
    },
  },
};
</script>

<style scoped>
.select2 {
  width: 100% !important;
}
</style>
